<template>
  <div class="container">
    <table class="table-todos">
      <caption>
        <h1>Список задач</h1>
      </caption>
      <template v-if="tasks.length > 0">
        <thead class="table-todos__headers">
          <tr>
            <th>№</th>
            <th>Описание</th>
            <th>Выполнено - {{ completedTasks }} задач</th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(task, index) in tasks" :key="index">
            <td>{{ index + 1 }}</td>
            <td :class="{ 'status-done': task.is_completed }">
              <p class="text-description">{{ task.description }}</p>
            </td>
            <td v-if="task.is_completed">Выполнено</td>
            <td v-else>Не выполнено</td>
            <td style="width: 50px">
                <button type="button" v-show="!task.is_completed"
                        class="btn-done-todo"
                        @click="taskDone(index)">&#10004;</button>
            </td>
            <td style="width: 50px">
              <button class="btn-close-todo" @click="removeTask(task)">&#10008;</button>
            </td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td colspan="5">
              <button type="button" @click="addTask" class="btn-add-task">
                Добавить задачу
              </button>
            </td>
          </tr>
        </tfoot>
      </template>
      <template v-else>
        <button type="button" @click="addTask" class="btn-add-task">Добавить задачу</button>
      </template>
    </table>
    <div v-show="modalWindow" class="modal-window-wrp">
      <div v-show="modalWindow" class="modal-window">
        <button type="button"
                class="btn-close-modal"
                @click="modalWindow = !modalWindow">&#10006;</button>
        <h2>Добавить задачу</h2>
        <div class="form-add-task">
          <label>Описание:
            <input :class="{ 'text-warning': textWarning }"
                   @keydown="userInput"
                   type="text"
                   v-model="textDescription"
                   placeholder="Завести задачу">
          </label>
          <label>
            <input type="checkbox" v-model="is_completed">Задача выполнена?
          </label>
          <button type="button" @click="saveTask" class="btn-add-task">Добавить</button>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  name: 'Todos',
  data() {
    return {
      tasks: [],
      modalWindow: false,
      textDescription: '',
      is_completed: false,
      completedTasks: 0,
      textWarning: false,
    };
  },
  methods: {
    addTask() {
      this.textDescription = '';
      this.textWarning = false;
      this.is_completed = false;
      this.modalWindow = true;
    },
    saveTask() {
      this.textDescription = this.textDescription.trim();
      if (this.textDescription === '') {
        this.textWarning = true;
        return;
      }
      this.modalWindow = false;
      const data = {
        uid: `f${(+new Date()).toString(16)}`,
        description: this.textDescription,
        is_completed: this.is_completed,
      };
      this.tasks.push(data);
      this.writeInStorage();
      this.checkCount();
    },
    removeTask(task) {
      this.tasks.splice(this.tasks.indexOf(task), 1);
      this.writeInStorage();
      this.checkCount();
    },
    userInput() {
      this.textWarning = false;
    },
    taskDone(index) {
      this.tasks[index].is_completed = true;
      this.writeInStorage();
      this.checkCount();
    },
    checkCount() {
      let count = 0;
      this.tasks.forEach((el) => {
        if (el.is_completed) {
          count += 1;
        }
      });
      this.completedTasks = count;
    },
    loadTasksList() {
      if (localStorage.getItem('tasks')) {
        this.tasks = JSON.parse(localStorage.getItem('tasks'));
      }
    },
    writeInStorage() {
      localStorage.setItem('tasks', JSON.stringify(this.tasks));
    },
  },
  mounted() {
    this.loadTasksList();
  },
};
</script>


